<template>
  <div>
    <h1></h1>
    <el-table :data="items">
      <el-table-column prop="_id" label="ID" width="240"></el-table-column>
      <el-table-column prop="title" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="180">
        <template slot-scope="scope">
          <el-button
            type="text"
            size="small"
            @click="$router.push(`/articles/edit/${scope.row._id}`)"
          ></el-button>
          <el-button type="text" size="small" @click="remove(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
      <el-pagination
      class="pagenation"
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage3"
      :page-size="pageSizes"
      layout="prev, pager, next, jumper"
      :total="totalCount"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: [],
       pageSizes: 10, //
      totalCount: 30, //
      currentPage3: 1, //
    };
  },
  methods: {
    handleCurrentChange(val) {
      this.currentPage3 = val;
        this.fetch()
    },
    async fetch() {
      const res = await this.$http.get("rest/articles",{
        params: { page: this.currentPage3, row: this.pageSizes },
      });
      this.items = res.data.all;
      this.totalCount=res.data.count
    },
    // 
    remove(row) {
      this.$confirm(` "${row.title}"`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(async () => {
         await this.$http.delete(`rest/articles/${row._id}`);
        this.$message({
          type: "success",
          message: "!"
        });
        this.fetch();
      });
    }
  },
  created() {
    this.fetch();
  }
};
</script>

